@keyframes captionRowAnimate {
  from {font-size: 1vw;}
  to {font-size: 2vw;}
}

@keyframes valueRowAnimate {
  from {font-size: 2vw;}
  to {font-size: 4vw;}
}

.captionRow{
  margin-top:50vh;
  display:flex;
  margin-left: 0;
  margin-right:0
}

.captionRow > div{
  color:rgb(181, 134, 4);
  width: 33%;
  text-align: center;
  
  font-size: 2vw;
  animation-name: captionRowAnimate;
  animation-duration: 1.5s;
}

.valueRow{
  
  display:flex;
  margin-left: 0;
  margin-right:0;
    font-weight:800 ;
  font-size: 4vw;
  animation-name: valueRowAnimate;
  animation-duration: 1.5s;

}
.valueRow > div{
  color:#ffffff;
  width: 33%;
  text-align: center;
}
/* Style the video: 100% width and height to cover the entire window */
#myVideo {
  position: fixed;
  right: 0;
  bottom: 0;
  height:100vh;
  width: 100vw;
  min-width: 100vw;
  min-height: 100vh;
  overflow: hidden;
  object-fit: cover;
}

/* Add some content at the bottom of the video/page */
.content {
  position: fixed;
  xbottom: 0;
  xbackground: rgba(0, 0, 0, 0.5);
  xcolor: #f1f1f1;
  width: 100%;
  padding: 20px;
}

/* Style the button used to pause/play the video */
#myBtn {
  width: 200px;
  font-size: 18px;
  padding: 10px;
  border: none;
  background: #000;
  color: #fff;
  cursor: pointer;
}

#myBtn:hover {
  background: #ddd;
  color: black;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
